import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { UserService } from 'src/app/users API';



@Component({
  selector: 'app-student-home',
  templateUrl: './student-home.page.html',
  styleUrls: ['./student-home.page.scss'],
})
export class StudentHomePage implements OnInit {
  userInfo
  username
  firstName
  lastName
  schoolName
  constructor(private authService: AuthenticationService, private userService: UserService) {}

  ngOnInit() { 
    //Gert the token to display user info in the rocket
    var token= localStorage.getItem('userStudent')
    var realToken= `token=${token},`;
    this.userInfo=this.userService.getProfileGet(realToken)
    .subscribe(info =>{
      console.log(info.classroom)
      this.username = info.username
      this.firstName = info.first_name
      this.lastName = info.last_name
      this.schoolName = info.school_name
    })
    
  }
  logout(){
    this.authService.logout();
  }
  
  }
